import { Exception, Type } from 'rcfm'
import Installer from './installer'
import store from './helpers/store'



// @param {Object} options
// @prop {string} options.name - An unique name
// @prop {Function} options.C(c)omponent | options.render - The render function.
// @prop {boolean} [options.force = false] - If setted to true, replaces existed
// component/section instead of raise an CONFLICTED_NAME exception.
// @returns {undefined}
Installer.prototype.install = function(options = {}) {
  let name = options.name
  let render = options.Component || options.component || options.render
  let force = 'force' in options ? options.force : false
  Type.mustbe.nonblankString(name, 'UI installer name')
  Type.mustbe.boolean(force, 'UI installer force option')
  Type.mustbe.function(render, 'UI installer component')
  let installed = store(this).installed
  !force && name in installed && Exception.raise({
    type: Installer.exceptions.CONFLICTED_NAME,
    message: `Componnet/Section '${name}' is already existed.
             Please set 'force' to true if the existed
             component/section should be replaced.`,
  })
  installed[name] = { name, force, render }
}
