import store from './helpers/store'



class Installer {

  static exceptions = {
    CONFLICTED_NAME: 'UI_INSTALLER_CONFLICTED_NAME',
    NOT_FOUND: 'UI_INSTALLER_TARGET_NOT_FOUND',
  }

  constructor() {
    store(this, { installed: {} })
  }

  static create(...args) { return new Installer(...args) }

}



export { Installer, Installer as default }
