import { Exception } from 'rcfm'
import Installer from './installer'
import store from './helpers/store'



Installer.prototype.use = function(name = '') {
  let installed = store(this).installed
  if (name in installed) { return installed[name].render }
  Exception.raise({
    type: Installer.exceptions.NOT_FOUND,
    message: `Component/Section '${name}' is not installed.`,
  })
}
